import { map, addIndex } from 'ramda';
import React, { useState } from 'react';
import styled, { ThemeProvider } from 'styled-components';

import { createMuiTheme } from '@material-ui/core/styles';
import { Drawer, Fab, List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/Inbox';
import StarredIcon from '@material-ui/icons/Star';
import SnoozedIcon from '@material-ui/icons/AccessTime';
import ImportantIcon from '@material-ui/icons/LabelImportant';
import SentIcon from '@material-ui/icons/Send';
import DraftsIcon from '@material-ui/icons/InsertDriveFile';

import create from './create.png';

const theme = createMuiTheme({ typography: { useNextVariants: true } });

const StyledDrawer = styled(Drawer)`
    && {
        padding-top: 10px;
        transition: ${ props => props.theme.transitions.create('max-width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        })};

        max-width: ${ props => props.open ? "240px" : (props.theme.spacing.unit * 7 + 1).toString() + "px" };
        overflow: hidden;

        & .paper {
            border-right: none;
            position: inherit;
        }
    }
`;

const StyledListItem = styled(ListItem)`
    && {
        height: 32px;
        transition: ${ props => props.theme.transitions.create('border-radius', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.enteringScreen,
        })};

        border-radius: ${ props => props.open ? "0px 16px 16px 0px" : "16px" };
        padding: ${ props => props.open ? "8px 16px 8px 16px" : "8px" };
        max-width: ${ props => props.open ? "inherit" : "40px" };

        letter-spacing: .2px;
        font-size: 0.875rem;
        font-weight: ${ props => props.selected ? "bold" : "normal" };
        overflow: hidden;
    }
`;

const MenuWrapper = styled.div`
    && {
        margin-top: 80px;
        max-width: 256px;
        display: flex;
        flex-direction: column;
    }
`;

const ComposeButton = styled(Fab)`
    && {
        background-color: #fff;
        box-shadow: 0 1px 2px 0 rgba(60,64,67,0.302), 0 1px 3px 1px rgba(60,64,67,0.149);
        color: #3c4043;
        max-width: 141px;
        display: inline-flex;
    }
`;

const ComposeButtonContentWrapper = styled.div`
    && {
        display: flex;
        margin-right: 25px;
        align-items: center;
        font-weight: 500;
        text-transform: none;
    }
`;

const CreateIcon = styled.div`
    && {
        background-image: url(${create});
        background-size: 32px;
        background-position: center;
        background-repeat: no-repeat;
        height: 48px;
        min-width: 56px;
    }
`;

const ClippedDrawer = props => {
    const [selectedList, setSelectedList] = useState([true, false, false, false, false, false]);
    const [currentSelected, setCurrentSelected] = useState(0);

    const listContent = [
        ["Inbox", <InboxIcon />, selectedList[0]],
        ["Starred", <StarredIcon />, selectedList[1]],
        ["Snoozed", <SnoozedIcon />, selectedList[2]],
        ["Important", <ImportantIcon />, selectedList[3]],
        ["Sent", <SentIcon />, selectedList[4]],
        ["Drafts", <DraftsIcon />, selectedList[5]],
    ]

    const updateSelected = index => {
        const newSelectedList = selectedList.slice();
        newSelectedList[currentSelected] = false;
        newSelectedList[index] = true;
        setCurrentSelected(index);
        setSelectedList(newSelectedList);
    }

    return (
        <ThemeProvider theme={theme}>
            <StyledDrawer
                variant="permanent"
                classes={{ paper: "paper" }}
                open={props.open}
            >
                <List dense>
                    {
                        addIndex(map)(([ text, icon, selected ], index) => (
                            <StyledListItem button key={text} selected={selected} open={props.open}
                                onClick={() => updateSelected(index)}
                            >
                                <ListItemIcon> {icon} </ListItemIcon>
                                <ListItemText disableTypography> {text} </ListItemText>
                            </StyledListItem>
                        ), listContent)
                    }
                </List>
            </StyledDrawer>
        </ThemeProvider>
    );
}

const Menu = props => {
    return (
        <MenuWrapper>
            <ComposeButton variant="extended">
                <ComposeButtonContentWrapper>
                    <CreateIcon></CreateIcon>
                    Compose
                </ComposeButtonContentWrapper>
            </ComposeButton>
            <ClippedDrawer open={props.menuToggle}/>
        </MenuWrapper>
    );
}

export default Menu;
